import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Card } from "react-bootstrap";
function Display(props) {
  return (
    <Card style={{ width: "22rem" }} className="my-4 border-primary mx-4">
      <Card.Body>
        <Card.Title>User {props.results.firstName}</Card.Title>
        <Card.Subtitle className="mb-2 text-muted">
          user_name: {props.results.firstName} {props.results.lastName}
        </Card.Subtitle>
        <Card.Text>
          <b>FirstName: {props.results.firstName}</b>
          <br />
          <b>LastName: {props.results.lastName}</b>
          <br />
          <b>Password: {props.results.password}</b>
          <br />
          <b>ConfirmPassword: {props.results.confirmPassword}</b>
          <br />
          <b>Email: {props.results.email}</b>
          <br />
          <b>Gender: {props.results.gender}</b>
          <br />
          <b>AcceptTandC: {props.results.acceptTandC}</b>
          <br />
          <b>Department: {props.results.department}</b>
        </Card.Text>
        <Card.Link href={`mailto:${props.results.email}`}>Send Mail</Card.Link>
      </Card.Body>
    </Card>
  );
}
export default Display;
